* {
  padding: 0;
  margin: 0;
  font-size: 26px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  user-select: none;
}

body {
  background: wheat;
}

.container {
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  min-height: 400px;
}

.horiz {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100px;
}

button {
  background: none;
  border: none;
  outline: none;
}

.timer {
  color: white;
}

#session-length,
#break-length {
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spread-horizontal {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#timer {
  border: 3px solid whitesmoke;
  border-radius: 50px;
  padding: 10px;
  height: 130px;
  background: darkorange;
  color: white;
}

.vert,
#timer,
#display {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#controls {
  width: 110px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#start_stop {
  width: 60px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

#reset {
  display: flex;
  align-items: flex-start;
}

#time-left {
  font-size: 48px;
}

p {
  margin: 0 5px 5px 5px;
}

h2 {
  font-size: 32px;
  position: relative;
  top: -30px;
  font-weight: bolder;
}
